  @font-face {
      font-family: 'Brusnika';
      font-style: 'normal';
      font-weight: 400;
      src: url(../../fonts/Brusnika-normal_version_02.otf);
  }
:root {
    --blog-bg-gradient: linear-gradient(90deg, #A6C0FE 0%, #F68084 100%);
    --button-gradient: linear-gradient(90deg, #F093FB 0%, #F5576C 100%);
    --business-bg-gradient: linear-gradient(90deg, #26A0DA  0%,  #314755 100%);
    --blur: 0;
    --pointer: 'unset';
}
.overlay {
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 20;
}
button, input {
    outline: none;
}
header {
    filter: blur(var(--blur));
    pointer-events: var(--pointer);
    height: 90px;
    padding-top: 5px;
}

.rounded {
    border-radius: 20px;
}
.logo-link {
    display: block;
    width: max-content;
    margin: 0 auto;
}
.header-logo {
    height: 50px;
}
.user-type-box {
    filter: blur(var(--blur));
    pointer-events: var(--pointer);
    padding-bottom: 50px;
}
.user-type-button-box {
    width: 50%;
    font-size: 100%;
    display: flex;
}
.user-type-buttons-box {
    height: 90px;
    display: flex;
}
.user-type-button-box:nth-child(1) {
    border-bottom-right-radius: 2em;
}
.user-type-button-box:nth-child(2) {
    border-bottom-left-radius: 2em;
}
.add-corners {
    position: relative;
}
.add-corners::before {
    position: absolute;
    content: '';
    width: 2em;
    height: 2em;
    background-image: url(./img/corner.svg);
    background-size: cover;
}
.add-corners::after {
    position: absolute;
    content: '';
    width: 2em;
    height: 2em;
    background-image: url(./img/corner.svg);
    background-size: cover;
    top: 0;
    right: 0;
    transform: rotate(90deg);
}

.user-type-button {
    width: 83%;
    height: 50px;
    border: 1px solid #5A5A5A;
    box-sizing: border-box;
    background: transparent;
    outline: transparent;
    margin: auto;
    display: block;
    color: #5A5A5A;
}
#active-btn {
    background: var(--button-gradient);
    border: transparent;
    color: #fff;
}
.picture-section {
    position: relative;
}
.picture-container {
    position: relative;
    padding-top: 50px;
    padding-bottom: 70px;
}
.picture-container-img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 63%;
    transition: .5s;
    transform: translateY(-50%);
    top: 50%;
}

.picture-container-img:nth-child(1) {
    position: relative;
    transform: unset;
    top: unset;
}
.text-input {
    border: none;
    background-color: #ffffff;
    width: 93%;
    height: 55px;
    padding: 20px;
    margin: 0 auto;
    margin-top: 20px;
    display: block;
}
.login-submit-btn {
    border: none;
    width: 93%;
    margin: 0 auto;
    margin-top: 20px;
    display: block;
    background: var(--button-gradient);
    color: #fff;
    padding: 20px;
}
.reset-password-link {
    font-size: 12px;
    text-align: center;
    display: block;
    margin: 0 auto;
    margin-top: 10px;
/* identical to box height */

    text-decoration-line: underline;

    color: #FFFFFF;
}
.sign-up-btn {
    border: 1px solid #FFFFFF;
    width: 93%;
    background: transparent;
    font-family: Montserrat;
    font-size: 14px;
    margin: 0 auto;
    margin-top: 20px;
    display: block;
    color: #fff;
    padding: 20px;
}
.login-text {
    font-family: Montserrat;
    font-size: 12px;
    line-height: 15px;
    text-align: center;
    display: block;
    margin: 0 auto;
    margin-top: 10px;
    color: #FFFFFF;
}
.social-media-box {
    width: 20%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    margin-top: 10px;
}
.social-media-box svg {
    width: 24px;
    height: 24px;
}
.social-media-button {
    background-color: transparent;
    border: none;
}
.about-us-text {
    text-align: center;
    margin-top: 30px;
    display: block;
    font-family: Montserrat;
    font-size: 14px;
    line-height: 17px;

    color: #FFFFFF;
}

.quotes-text {
opacity: 0;
position: absolute;
top: 50%;
right: 10%;
font-family: Brusnika;
font-style: normal;
font-weight: 500;
font-size: 27px;
line-height: 28px;
color: #FFFFFF;
transform-origin: center center;
transform: rotate(-22.96deg);
transition: 1s;
}
@media screen and (max-width:1200px) {
    .business > .picture-container-img{
        right: 0;
        left: unset;
        margin-left: 40%;
    }
}

@media screen and (min-width:1200px) {
    body {
        min-height: 100vh;
    }
    .header-logo {
        height: 80px;
    }
    .user-type-box {
        min-height: 100vh;
        overflow: hidden;
    }
    .user-type-button {
        width: 40%;
    }
    .quotes-text {
        font-size: 38px;
    }
    .login-section {
        display: flex;
        justify-content: space-between;
        position: relative;
        align-items: center;
    }
    .about-us-text {
        font-size: 24px;
    }
    .about-us-text::after {
        content: '';
        display: inline-block;
        background-image: url(./img/arrow.svg);
        height: 24px;
        width: 10px;
        background-size: contain;
        background-repeat: no-repeat;
        transform: translateY(5px) translateX(10px);
    }
    .registration-container {
        width: 50%;
        padding: 0 10%;
    }
    .picture-section {
        width: 50%;
    }
    .picture-container-img {
        width: 80%;
    }
    .login-type-business {
        flex-direction: row-reverse;
    }
    .business > .picture-container-img{
        right: 0;
        left: unset;
        margin-left: 10%;
    }
}