.modal-window {
    width: 100%;
    background-color: #fff;
    margin-top: 100px;
}
.default-input {
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 20px;
    padding: 15px 20px;
    color: #8A8A8A;
    border: none;
}
.photo-and-user-data-box {
    display: flex;
    justify-content: space-between;
}
.photo-box {
    width: 31%;
}
.photo-wrapper {
    width: 100%;
    height: auto;
    border-radius: 50%;
    overflow: hidden;
}
.photo-container {
    position: relative;
}
.plus-btn {
    position: absolute;
    bottom: 12%;
    right: 12%;
    width: 25%;
    height: 25%;
    background: #333333;
    border-radius: 50%;
    border: none;
}
.plus-btn svg {
    width: 50%;
    height: 50%;
}
.profile-photo {
    width: 100%;
}
.modal-social-media-icons {
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.social-media-icon {
    margin: 0 auto;
    width: 30%;
    height: auto;
    margin-top: 30px;
}
.user-data-field {
    width: 56%;
    padding: 30px 20px;
} 
.top-input {
    width: 100%;
    margin-top: 15px;
}
.gender-form {
    margin-top: 20px;
}
.gender-form input {
    display: none;
}
.gender-form {
    width: 100%;
    max-width: 190px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: #B4B4B4;
}
.gender-label::before {
    display: inline-block;
    margin-right: 5px;
    content: '';
    width: 20px;
    height: 20px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.gender-label:nth-child(1)::before {
    background-image: url(./img/gender-male.svg);
}
.gender-label:nth-child(2)::before {
    background-image: url(./img/gender-female.svg);
}
.gender-text {
    margin-top: 20px;
    font-size: 12px;
    /* or 15px */

    letter-spacing: 0.02em;

    color: #B4B4B4;
}
.data-input-box {
    display: flex;
}
.date-input {
    width: 65px;
    margin-right: 5px;
    margin-top: 10px;
}
.year-input {
    width: 50%;
    /* min-width: 160px; */
}
.bottom-input {
    width: 100%;
}